import PressCell from 'press-ui/press-cell/press-cell.vue';
import PressList from 'press-ui/press-list/press-list.vue';
import PressPickerPlus from 'press-ui/press-picker-plus/press-picker-plus.vue';
import PressPopupPlusCell from 'press-ui/press-popup-cell/press-popup-cell.vue';

import { showCustomPopup, local } from 'src/packages/press-list/demo-helper/custom';
import { virtualListMixin } from 'src/packages/press-list/demo-helper/virtual-list-mixin';
import {
  FUNCTIONAL_ID_MAP,
} from 'src/packages/press-popup-cell/demo-helper/helper';


function fetchData({ length, delay, beforeLength }) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        data: Array.from({ length }).map((_, i) => {
          let value = i + beforeLength + 1;
          if (value < 10) {
            value = `0${value}`;
          }
          return {
            label: value,
            value,
          };
        }),
      });
    }, delay || 0);
  });
}

export default {
  i18n: {
    'zh-CN': {
      custom: '自定义',
    },
    'en-US': {
      custom: 'Custom',
    },
  },
  components: {
    PressList,
    PressPopupPlusCell,
    PressPickerPlus,
    PressCell,
  },
  mixins: [virtualListMixin()],
  data() {
    const listLocal = {
      ...local,
    };
    let listStyle = '';




    return {
      sectionStyle: 'margin: 0;background: #f7f8fa;',
      headerStyle: 'background: #f7f8fa;',

      loading: false,
      finished: false,
      immediateCheck: true,
      list: [],

      FUNCTIONAL_ID_MAP,

      listLocal,
      listStyle,
    };
  },
  computed: {
  },
  watch: {
    listLocal: {
      handler(value) {
        if (!value.vertical) {
          this.useVirtualList = false;
        }
      },
      deep: true,
    },
  },
  mounted() {
    // this.loading = true;
    // this.onFetchData(true);
  },
  methods: {
    load() {
      console.log('[load]');
      this.onFetchData();
    },
    scroll(event, scrollerHeight) {
      // console.log('[scroll]', event, scrollerHeight);
      this.updateCurrentIndex(event, scrollerHeight);
    },
    onFetchData(isRefresh) {
      const { total, pageSize, delay } = this.listLocal;

      fetchData({
        delay,
        length: pageSize,
        beforeLength: isRefresh ? 0 : this.list?.length || 0,
      }).then(({ data }) => {
        let list = isRefresh ? data : [
          ...this.list,
          ...data,
        ];
        if (list.length > total) {
          list = list.slice(0, total);
        }

        this.list = list;
        console.log('[current total]', list.length);
        this.loading = false;
        this.finished = this.list.length >= total;
      })
        .catch(() => {});
    },

    onShowBasicPopupCell() {
      showCustomPopup({
        context: this,
        callback: {
          changeTotal: this.changeTotal,
          changePageSize: this.changePageSize,
          changeDelay: this.changeDelay,
          changeDirection: this.changeDirection,
          changeAutoCheckScroller: this.changeAutoCheckScroller,
        },
      });
    },
    changePageSize(pageSize) {
      this.listLocal = {
        ...this.listLocal,
        pageSize,
      };
      this.generateData();
    },
    changeTotal(total) {
      this.listLocal = {
        ...this.listLocal,
        total,
      };
      this.generateData();
    },
    changeDelay(delay) {
      this.listLocal = {
        ...this.listLocal,
        delay,
      };
      this.generateData();
    },
    changeDirection(vertical) {
      this.listLocal = {
        ...this.listLocal,
        vertical,
      };
      this.generateData();
    },
    changeAutoCheckScroller(autoCheckScroller) {
      this.listLocal = {
        ...this.listLocal,
        autoCheckScroller,
      };
      this.list = [];
      this.generateData();
    },
    generateData() {
      this.loading = true;
      this.onFetchData(true);
    },
  },
};
